<script>
    import simpleParallax from 'simple-parallax-js';
    import { gsap } from "gsap";
    import { ScrollTrigger } from "gsap/ScrollTrigger";

    gsap.registerPlugin(ScrollTrigger);

    export default {
        data() {
            return {
            };
        },
        mounted() {
            var img_right = document.getElementsByClassName('parallax-right');
            new simpleParallax(img_right, {
                delay: 5,
                scale: 1.2,
                transition: 'cubic-bezier(0,.8,1,.25)',
                overflow: true,
                customWrapper: '.img-right',
                orientation: 'right',
                maxTransition: 50
            });

            ScrollTrigger.matchMedia({
                "(min-width: 651px)": function() {
                    gsap.from(".parallax-left", {
                        x: '20',
                        scrollTrigger: {
                            trigger: ".index",
                            start: "top top",
                            end: "bottom top",
                            scrub: 1.5,
                        },
                    });
                    gsap.from(".img-font", {
                        opacity: 0,
                        y: 20,
                        scrollTrigger: {
                            trigger: ".index",
                            start: "top top",
                            end: "bottom top",
                            scrub: true,
                        },
                    });
                    gsap.from(".intro-title-title", {
                        opacity: 0,
                        y: 20,
                        scrollTrigger: {
                            trigger: ".index",
                            start: "top top",
                            end: "bottom top",
                            scrub: true,
                        },
                    });
                },
                "(max-width: 650px)": function() {
                    gsap.to(".parallax-left", {
                        x: '-23',
                        scrollTrigger: {
                            onToggle: self => console.log("INTRO:", self.isActive),
                            trigger: ".index",
                            start: "bottom top",
                            end: "+=400px",
                            scrub: 1.5,
                        },
                    });
                },
            });


        },
    };
</script>

