<template>
  <div :class="{isLoading:isLoading}">
    <router-view></router-view>
  </div>
</template>
<script>
  import '@/styles/style.scss';
  import { gsap } from "gsap";
    
  export default {
        data() {
            return {
                isLoading: true,
            };
        },
        mounted() {
            gsap.to(".top-and", {
                        opacity:'1',
                        duration: 1.5,
                        ease:'Power1.easeIn',
            });
            this.timeout = setTimeout(() => {
                let tl =gsap.timeline({repeat: 0});
                tl.to(".display-h1 span", {
                        opacity:'1',
                        duration: 0.8,
                        stagger: 0.1,
                        ease:'power1.inOut',
                }) 
                .to(".top", {
                        height:'auto',
                        duration: 0.8,
                        // delay:0.1,
                        ease:'power4.out',      
                })
                .from(".main-right", {
                        opacity:0,
                        duration: 0.7,
                        delay:-0.3,
                        ease:'power1.inOut',
                })
                .from(".index-parallax", {
                        y:'30',
                        opacity:0,
                        duration: 0.9,
                        delay:-0.4,
                        ease:'Sine.easeOute',
                        onReverseComplete: this.isLoading = false,    
                });  
            }, 950);
        },
    };
</script>

<style scoped>
  .isLoading{
    height: 100vh;
    overflow: hidden;
  }
</style>
