<template>
    <div class="index">
        <section class="main outer">
            <img class="bg-img" src="../assets/index_bg.png">
            <div class="container">
                <div class="index-parallax">
                    <div class="main-left">
                        <img class="main-left__dec main-left__backend" src="../assets/index_main_backend.png">
                        <img class="main-left__img" src="../assets/index_main.jpg">
                        <img class="main-left__dec main-left__front" src="../assets/index_main_front.png">
                        <div class="main-left__dec rotate">
                            <img src="../assets/index_rotate_ring.svg">
                            <img class="text" src="../assets/index_rotate_text.svg">
                        </div>
                    </div>
                </div>
                <div class="main-right">
                    <img src="../assets/index_title.svg">
                    <div class="hr"></div>
                </div>
            </div>
        </section>
    </div>
</template>
  
<script>
// import simpleParallax from 'simple-parallax-js';
import { gsap } from "gsap";
import { ScrollTrigger } from "gsap/ScrollTrigger";

gsap.registerPlugin(ScrollTrigger);

  export default {
    data() {
      return {
      };
    },
    mounted() {
        ScrollTrigger.matchMedia({
            "(min-width: 751px)": function() {
                gsap.to(".bg-img", {
                    opacity: 0,
                    scrollTrigger: {
                        trigger: "body",
                        start: "top top",
                        end: "+=400",
                        scrub: true,
                    },
                });
                gsap.to(".index-parallax", {
                    y: '-15%',
                    scrollTrigger: {
                        trigger: "body",
                        start: "top top",
                        end: "50%",
                        scrub: 3.5,
                    },
                });
            },
            "(max-width: 750px)": function() {
                gsap.to(".bg-img", {
                        opacity: 0,
                        scrollTrigger: {
                            trigger: "body",
                            start: "top top",
                            end: "+=300",
                            scrub: true,
                    },
                });
                gsap.to(".index-parallax", {
                    y: '-100px',
                    scrollTrigger: {
                        trigger: "body",
                        start: "top top",
                        end: "+=450",
                        scrub: 2,
                    },
                });
                gsap.from(".rotate", {
                        opacity: 0,
                        y:80,
                        scrollTrigger: {
                            trigger: ".body",
                            start: "top top",
                            end: "+=300",
                            scrub: true,
                    },
                });
            },
        });
    },
  };
</script>
  
