<template>
    <section class="time-outer">
        <div class="time-card">
            <div class="img-outer">
                <div class="left-img time-img">
                    <img class="left-img-pc" src="info-time-img-pc.png">
                    <img class="left-img-mb" src="info-time-img-mb.png">
                </div>
            </div>
            <div class="right-img">
                <img class="info-date" src="info-time-date.png">
                <img class="info-time" src="info-time-time.png">
            </div>
        </div>
    </section>
</template>
  
<script>
    import simpleParallax from 'simple-parallax-js';
    import { gsap } from "gsap";
    import { ScrollTrigger } from "gsap/ScrollTrigger";

    gsap.registerPlugin(ScrollTrigger);

    export default {
        data() {
            return {
            };
        },
        mounted() {
            var left_img_pc = document.getElementsByClassName('left-img-pc');
            new simpleParallax(left_img_pc, {
                delay: 0.08,
                scale: 1.09,
                transition: 'cubic-bezier(0,.8,1,.25)',
                orientation: 'left',
                customWrapper: '.left-img',
            });

            ScrollTrigger.matchMedia({
                "(max-width: 650px)": function() {
                    gsap.from(".info-date", {
                        y:90,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: ".info-list-item-time",
                            start: "center bottom",
                            end: "+=300",
                            scrub: 2,
                            ease: 'Sine.easeOute',
                        },
                    });    
                    gsap.from(".info-time", {
                        y:90,
                        opacity: 0,
                        scrollTrigger: {
                            trigger: ".info-list-item-time",
                            start: "center bottom",
                            end: "+=350",
                            scrub: 2,
                            delay: 2,
                            ease: 'Sine.easeOute',
                        },
                    });
                    gsap.to(".left-img-mb", {
                        y:20,
                        scrollTrigger: {
                            trigger: ".info-list-item-time",
                            start: "top bottom",
                            end: "+=900",
                            scrub: 15,
                            ease: 'cubic-bezier(0,.36,.5,.99)',
                        },
                    });
                }
             });
        }
    };
</script>

